.App {
  font-family: sans-serif;
  text-align: center;
}

.main-nav {
  background-color: aquamarine;
  margin: 0;
  padding: 0;
  border: 0;
  display: flex;
  justify-content: space-between;
}

.main-nav li {
  list-style: none;
  padding: 1rem;
  flex-grow: 1;
  cursor: pointer;
  font-weight: 500;
  font-size: 1.2rem;
}

.main-nav li:hover {
  background-color: coral;
}

.MathBoard {
  padding: 5rem;
  font-size: 4rem;
}

.MathAnswerBox {
  display: inline;
}

.MathAnswerBox input {
  font-size: 4rem;
  width: 7rem;
  text-align: center;
  cursor: pointer;
  margin-left: 1rem;
  margin-right: 1rem;
}

.MathAnswerBox input.correct {
  color: green;
}

.MathAnswerBox input.wrong {
  color: red;
}

.MathNextProblem {
  margin-top: 2rem;
  font-size: 2rem;
  border: solid 1px;
}
